.toolbar {
  display: flex;
  justify-content: flex-end;
  align-items: center;

  label,
  input,
  select {
    font-size: 12px;
  }

  input,
  select {
    padding: 2px 4px;
  }

  input {
    margin-left: 8px;
    margin-right: 24px;
  }

  select {
    margin-left: 8px;
  }
}
